.remove-background-container {
  text-align: center;
  padding: 20px;
}

input[type="file"] {
  margin-bottom: 20px;
}

.image-preview {
  display: flex;
  justify-content: space-around;
  margin-top: 20px;
}

.image-preview img {
  max-width: 100%;
  height: auto;
  border: 1px solid #ccc;
}

button {
  margin-top: 20px;
  padding: 10px 20px;
  background-color: #007bff;
  color: white;
  border: none;
  cursor: pointer;
}

button:hover {
  background-color: #0056b3;
}